<template>
  <div class="wyl_big">
    <!-- header  面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- header end -->
    <!-- main  卡牌试图区域 -->
    <el-card class="box-card">
      <div style="margin-top: 15px;">
        <!-- serach   button 搜索与添加区域 -->
        <el-row :gutter="20">
          <!-- search  button -->
          <el-col :span="8">
            <el-input
              clearable
              v-model="queryInfo.query"
              placeholder="请输入内容"
              class="input-with-select"
              @clear="getUserList"
            >
              <el-button @click="getUserList" slot="append" icon="el-icon-search"></el-button>
            </el-input>
          </el-col>
          <!-- button  -->
          <el-col :span="4">
            <el-button type="primary" @click="dialogVisible=true">添加用户</el-button>
          </el-col>
        </el-row>

        <!-- 用户列表区域 -->
        <el-table :data="UserList" border stripe style="width: 100%">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="username" label="姓名" width="180"></el-table-column>
          <el-table-column prop="email" label="邮箱" width="180"></el-table-column>
          <el-table-column prop="mobile" label="电话"></el-table-column>
          <el-table-column prop="role_name" label="角色"></el-table-column>
          <el-table-column prop="mg_state" label="状态">
            <template slot-scope="scope">
              <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="240">
            <template slot-scope="scope">
              <!-- {{scope.row.id}} -->
              <el-tooltip
                :enterable="false"
                class="item"
                effect="dark"
                content="编辑按钮"
                placement="top"
              >
                <el-button
                  @click="showEditDialog(scope.row)"
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                :enterable="false"
                class="item"
                effect="dark"
                content="删除按钮"
                placement="top"
              >
                <el-button
                  @click="removeUserById(scope.row.id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                ></el-button>
              </el-tooltip>
              <el-tooltip
                :enterable="false"
                class="item"
                effect="dark"
                content="分配角色"
                placement="top"
              >
                <el-button
                  @click="setUser(scope.row)"
                  size="mini"
                  type="warning"
                  icon="el-icon-setting"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!-- 分页器 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="Total"
      ></el-pagination>
    </el-card>
    <!-- main  end -->
    <!-- 添加新用户的模态框  start -->
    <el-dialog title="添加用户" :visible.sync="dialogVisible" width="50%" @close="addDialogClose">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUesr">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 添加新用户的模态框  end -->

    <!-- 编辑新用户  start -->
    <el-dialog title="编辑用户" @close="editDialogClose" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑新用户   end -->

    <!-- 分配新用户  start-->
    <el-dialog
      title="分配用户"
      @close="setRolesDialogClose"
      :visible.sync="SetUserDialogVisible"
      width="50%"
    >
      <div>
        <p>当前的用户:{{UserInfo.username}}</p>
        <p>当前的角色:{{UserInfo.role_name}}</p>
        <p>
          分配新角色:
          <el-select v-model="selectedRoleId" placeholder="请选择">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="SetUserDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveInfo">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 分配新用户  end-->
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      //获取用户列表的参数
      queryInfo: {
        query: "",
        // 当前的的页数
        pagenum: 1,
        // 当前每页显示多少条数据
        pagesize: 2
      },
      // uers数据接口渲染   users   total
      UserList: [],
      Total: 0,
      // 模态框 添加新用户显示隐藏
      dialogVisible: false,
      // 模态框 添加新用户 数据接口
      addForm: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      //
      addFormRules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 2, max: 15, message: "长度在 2 到 15 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ]
      },
      //编辑用户
      editDialogVisible: false,
      // 查询到的用户信息对象
      editForm: {},
      editFormRules: {
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ]
      },
      // 分配新用户  显示隐藏
      SetUserDialogVisible: false,
      // 需要被分配角色的用户信息
      UserInfo: "",
      // 所有角色的数据列表
      rolesList: [],
      // 宜选中的角色Id值
      selectedRoleId: ""
    };
  },
  props: {},
  components: {},
  mounted() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      let { data: res } = await this.$http.get(`users`, {
        params: this.queryInfo
      });

      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      } else {
        this.UserList = res.data.users;
        this.Total = res.data.total;
      }
    },
    // 监听switch 数据变化
    async userStateChanged(a) {
      console.log(a.mg_state);
      let { data: res } = await this.$http.put(
        `users/${a.id}/state/${a.mg_state}`
      );
      console.log(a);
      if (res.meta.status !== 200) {
        a.mg_state != a.mg_state;
        return this.$message.error("更新用户状态失败");
      } else {
        this.$message.success("用户状态更改成功");
      }
    },

    // 分页器 监听pageSize 改变
    handleSizeChange(a) {
      console.log(a);
      this.queryInfo.pagesize = a;
      this.getUserList();
    },
    // 分页器 监听页码值 改变
    handleCurrentChange(a) {
      console.log(a);
      this.queryInfo.pagenum = a;
      this.getUserList();
    },

    // 监听 添加用户对话框的关闭事件
    addDialogClose() {
      this.$refs.addFormRef.resetFields();
    },
    //点击按钮  添加新用户
    addUesr() {
      this.$refs.addFormRef.validate(async valid => {
        // console.log(valid);
        if (!valid) {
          return false;
        }
        console.log(this.addForm);
        let { data: res } = await this.$http.post(`users`, this.addForm);
        console.log(res);
        if (res.meta.status !== 201) {
          this.$message.error("添加用户失败");
          console.log(res.meta.msg);
        } else {
          this.$message.success("添加用户成功");
          this.dialogVisible = false;
          this.getUserList();
        }
      });
    },

    // 编辑
    async showEditDialog(a) {
      let { data: res } = await this.$http.get(`users/${a.id}`);
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("编辑用户失败");
      } else {
        this.$message.success(res.meta.msg);
        this.editForm = res.data;
        this.editDialogVisible = true;
      }
    },
    // 监听修改  编辑
    editDialogClose() {
      this.$refs.editFormRef.resetFields();
    },
    //编辑里面进行验证
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        // console.log(valid);
        if (!valid) {
          return false;
        }
        let { data: res } = await this.$http.put(`users/${this.editForm.id}`, {
          email: this.editForm.email,
          mobile: this.editForm.mobile
        });
        console.log(res);
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg);
        } else {
          this.$message.success(res.meta.msg);
          (this.editDialogVisible = false), this.getUserList();
        }
      });
    },

    // 删除 页面   提示消息
    async removeUserById(id) {
      // console.log(id)
      let con = await this.$confirm(
        "此操作将永久删除该用户, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);

      if (con !== "confirm") {
        return this.$message.info("已取消删除");
      }

      let { data: res } = await this.$http.delete(`users/${id}`);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      } else {
        this.$message.success("用户删除成功");
        this.getUserList();
      }
    },

    // 展示分配角色的对话框
    async setUser(UserInfo) {
      this.UserInfo = UserInfo;
      //   在展示对话框之前 要进行所有数据的保存
      let { data: res } = await this.$http.get(`roles`);
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.$message.success(res.meta.msg);
      this.rolesList = res.data;
      this.SetUserDialogVisible = true;
    },
    // 点击按钮 分配角色 的 确认按钮
    async saveInfo() {
      if (!this.selectedRoleId) {
        return this.$message.error("请选择要分配的角色");
      }

      let { data: res } = await this.$http.put(
        `users/${this.UserInfo.id}/role`,
        {
          rid: this.selectedRoleId
        }
      );
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.$message.success(res.meta.msg);
      this.getUserList();
      this.SetUserDialogVisible = false;
    },
    // 监听对话框的关闭事件
    setRolesDialogClose() {
      this.selectedRoleId = "";
      this.UserInfo = "";
    }
  }
};
</script>

<style scoped lang='scss'>
.wyl_big {
  font-size: 0.32rem;
  .el-breadcrumb {
    font-size: 0.32rem;
  }
}
</style>